/* custom CSS styles */
@import url(https://fonts.googleapis.com/css?family=Roboto:400,300,300italic,400italic,500,500italic,700,700italic);
@import url(//fonts.googleapis.com/css?family=Open+Sans:400,300,600,700,700italic,400italic);
@import url(http://fonts.googleapis.com/css?family=PT+Serif);

  body {
    font-family: "Roboto", sans-serif;
    text-rendering: optimizeLegibility;
    background-color: #a9a9a9;
  }
  .row{
    max-width: 80em;
  }
  a {
    color: #0f654a;
  }
  a:hover{
    text-decoration: underline;
  }
  a:hover, a:focus {
    color: #0f654a;
  }
  author{
    font-style: italic;
  }
  .notbold {
    font-weight:400;
  }
  .nopadding {
    padding: 0;
  }
  .notoppadding {
    padding-top: 0;
  }
  .nobottompadding {
    padding-bottom: 0;
  }
  .noleftpadding{
    padding-left: 0;
  }
  .norightpadding{
    padding-right: 0;
  }
  .nohorizontalpadding{
    padding-right: 0;
    padding-left: 0;
  }
  .manageleftpadding{
    padding-left: .6rem;
  }
  /*.managehorizontalpadding{
  padding-left: .6rem;
  padding-right: .6rem;
}*/
.green {
  color: #0f654a;
}
.featurepadding{}
.goright{
  float:right;
}
.subhead-title{
  margin: 0 0 .5rem 0;
  padding: 0;
  color: #666;
  text-transform:uppercase;
}
.smaller-title {
  font-size: 60%;
  color: #666;
  margin-left: .3rem;
}
.smaller-title:hover {
  color: #333;
  text-decoration: none;
}
.story-author{
  font-style:italic;
}
/*TRUEMU*/
span.truemu{
  color: #036937;
  font-weight: 600;
}
span.truemu::before{
  color: #c1d82e;
  content:"TRU";
}
a.emu-button{
  /* margin: .5rem 0;
  padding: .8rem 1rem;*/
  background-color: #1e9744;
  text-transform: uppercase;
  font-size:1rem;
  text-decoration: none;
  color: #fff;
}
a:hover.emu-button{
  background-color: #0f654a;
  text-decoration: none;
}
a.allcaps {
  text-transform: uppercase;
}
/*Headings*/
h1{
  font-size: 2.1rem;
}
h2{
  font-size: 1.8rem;
}
h3{
  font-size: 1.5rem;
}
h4{
  font-size: 1.2rem;
}
h5{
  font-size: 1.1rem;
}
h6{
  font-size: 1rem;
}
ul.social-icons {
  list-style: none;
  float:right;
  padding: 0;
  margin: 0;
}
ul.social-icons li{
  padding:0;
  margin: 0 0 0 .1rem;
  display: inline-block;
}
ul.social-icons li img{
  padding:0;
  margin: 0;
  float: none;
}
#content-area{
  position: relative;
  padding: 0;
  margin: 0;
  width: 100%;
  background-color: #fff;
}
#connection-bar {
  width: 100%;
  z-index: 1000;
  position: relative;
  overflow: hidden;
}
#connection-bar a:hover {
  text-decoration: none;
}
#connection-bar ul li{
  display: inline;
  text-transform: uppercase;
  list-style: none;
}
#white-bar {
  width: 100%;
  background-color: #fff;
  z-index: 1000;
  position: relative;
  height: auto;
}
#transparent-bar{
  position:relative;
  width: 100%;
  background-color: rgba(0, 0, 0, .5);
  z-index: 100;
  height: auto;
}
#secondary-bar{
  position:relative;
  width: 100%;
  background-color: #fff;
  z-index: 100;
  height: auto;
  color: #333;
}
#tier1-nav{
  z-index: 200;
  padding: 0;
}
#tier1-nav h1{
  font-weight: 400;
  font-size: 2rem;
  margin: .3rem 0 0;
  padding: 0;
}
#tier1-nav h1 a{
  color: #333;
}
#tier1-nav h1 span.first-word{
  font-weight: 500;
  text-transform: uppercase;
  font-size: 2rem;
  color: #0f654a;
}
#tier1-nav ul{
  float: right;
  padding-top: 1.2rem;
  margin: 0;
  font-size: 1rem;
  text-align: right;
}
#tier1-nav ul li a{
  color: #333;
  padding: 0 .3rem;
}
#tier1-nav ul li a:hover{
  color: #0f654a;
}
#tier1-nav ul li a::after{
  content: url('../images/nav-arrow.png');
}
#tier1-nav ul li a:hover::after{
  content: url('../images/nav-arrow-over.png');
}
#tier1-nav ul li a.search-glass::after{
  content: url('../images/nav-search.png');
}
#tier1-nav ul li a.search-glass:hover::after{
  content: url('../images/nav-search-over.png');
}
#tier1-nav ul li a.menu-icon::after{
  content: url('../images/menu.png');
}
#tier1-nav ul li a.menu-icon:hover::after{
  content: url('../images/menu-over.png');
}
#tier1-nav ul li.menu-area {
  display: none;
}
#tier2-nav{
  color: #fff;
  z-index: 1000;
}
#tier2-nav ul{
  float: right;
  padding: .4rem 0;
  margin: 0;
  font-size: .8rem;
}
#tier2-nav ul li a{
  color: #fff;
  padding: 0 .6rem 0 .8rem;
  position:relative;
}
#tier2-nav ul li a:hover{
  color: #7afe19;
}
#tier2-nav ul li a.email-arrow::after{
  width: 8px;
  padding-top: 0;
  margin-top: 0;
  padding-left: 3px;
  content: url('../images/subnav-arrow.png');
}
#tier2-nav ul li a.email-arrow:hover::after{
  content: url('../images/subnav-arrow-over.png');
}
#tier2-nav ul li a.right-arrow{
  background-image: url('../images/subnav-arrow-right.png');
  background-repeat: no-repeat;
  background-position: 0 center;
}
#tier2-nav ul li a:hover.right-arrow{
  background-image: url('../images/subnav-arrow-right-over.png');
  background-repeat: no-repeat;
  background-position: 0 center;
}
