#tier3-nav ul{
  float: right;
  padding: .4rem 0;
  margin: 0;
  font-size: .8rem;
}
#tier3-nav ul li a{
  color: #333;
  padding: 0 .4rem;
}
#tier3-nav ul li a:hover{
  color: #7afe19;
}
#logo-box{
  background-color: #0f654a;
  /*padding: .8rem .6rem 0rem; */
  position: absolute;
  z-index: 200;
  box-sizing: border-box;
  left: 0;
  width: 100%;
  padding: 1rem;
  margin-right: 1rem;
}
#logo-box img{
  width: 100%;
}
#logo-box img.block-e{
  display: none;
}
#logo-box img.emu{
  display: none;
}
/*Feature story*/
.news-caps, .cal-caps{
  text-transform: uppercase;
}
#news-bar  {
  height: auto;
  position: relative;
  width: 100%;
  z-index: 10;
  padding: 1.3rem 0 1rem 0;
}
#news-bar img{
  width: 100%;
}
#featured-text p.button-group{
  text-align:left;
  padding-top: .5rem;
}
#featured-text a.button {
  margin: 0;
  padding: .5rem 1rem;
  background-color: #1e9744;
  text-transform: uppercase;
  font-size:.9rem;
  text-decoration: none;
}
#featured-text a:hover.button {
  background-color: #0f654a;
  text-decoration: none;
}
#featured-text{
  padding-top: 2.4rem;
  box-sizing: border-box;
}
#featured-text h3 {
  color:#0f654a;
}
#featured-text p{
  margin-bottom: .8rem;
  font-size: 1rem;
  line-height: 1.4rem;
}
#title-space  {
  height: 25rem;
  position: relative;
  width: 100%;
  z-index: 20;
}
/*Four story bar*/
#four-stories-bar{
  background-color: #f2f2f3;
  height: auto;
  color: #333;
  position: relative;
  width: 100%;
  z-index: 10;
  padding: .8rem 0;
}
#four-stories-bar li img.topic-image{
  width: 100%;
}
#four-stories-bar h5{
  text-transform: uppercase;
}
#four-stories-bar li p.button-group{
  text-align:center;
  padding-top: .5rem;
}
#four-stories-bar a.button {
  margin: 0;
  padding: .5rem 1rem;
  background-color: #1e9744;
  text-transform: uppercase;
  font-size:.9rem;
  text-decoration: none;
}
#four-stories-bar a:hover.button {
  background-color: #0f654a;
  text-decoration: none;
}
img.news-icon{
  padding: 0;
  margin: -2px 0 0;
}
#four-stories-bar li p{
  font-size: .9rem;
  line-height: 1.2rem;
  padding: .2rem 0;
  background-color: #fff;
  margin: 0;
}
#four-stories-bar .stories-content {
  margin: 0;
  padding: .5rem .8rem .8rem;
  background-color: #fff;
}
.stories-text-content p {
  text-align: left;
}

/*News Headline bar*/
#news-headline-bar{
  height: auto;
  color: #333;
  position: relative;
  width: 100%;
  z-index: 10;
  padding: .8rem 0;
}
#newshub-headlines-front{}
#newshub-headlines-front ul{
  list-style: none;
  margin:0;
  color: #333;
}
#newshub-headlines-front ul li{
  border-bottom: 1px dotted #ccc;
}
#newshub-headlines-front ul li:last-of-type{
  border-bottom: none;
}
#newshub-headlines-front ul li a{
  display: block;
  padding: .4rem 0;
  color: #333;
  line-height: 1.4rem;
}
#newshub-headlines-front ul li a:hover{
  text-decoration: none;
  background-color: #f8f8f8;
}
/*feature boxes*/
.featured-content-block {
  padding: 1.5rem 1.5rem 1.1rem 1.5rem;
  background-color: #f2f2f3;
  position: relative;
  margin:1.2rem 0 0 0;
}
div.featured-content-block + div.featured-content-block {
  margin: 2rem 0;
}
.featured-content-block img{
  margin-bottom: .6rem;
  width: 100%;
}
.featured-content-block p{
  font-size: .9rem;
  line-height: 1.2rem;
  margin-bottom: .3rem;
}
.featured-content-block h6.headline-block{
  display: block;
  background-color: #0c6348;
  padding: .2rem .4rem;
  color: #fff;
  text-transform: uppercase;
  position: absolute;
  left: 0;
  top: -1.2rem;
}
.featured-content-block h6.lt-green{
  background-color: #1e9744;
}
.featured-content-block span.truemu{
  color: #ffffff;
}
.featured-content-block a{
  color:#333;
}
ul.feature-list{
  list-style: none;
  margin: 0;
  color: #333;
  font-size: .9rem;
  padding: 0;
}
ul.feature-list li{
  border-bottom: 1px dotted #ccc;
  line-height: 1.2rem;
}
ul.feature-list li:last-of-type{
  border-bottom: none;
}
ul.feature-list li a{
  display: block;
  padding: .4rem 0;
  color: #333;
}
ul.feature-list li a:hover{
  text-decoration: none;
  background-color: #f8f8f8;
}
/*Calendar-feature*/

.calendar-feature-block{
  padding: 0rem 0rem 1.1rem 0rem;
  background-color: none;
  position: relative;
  margin: 0rem 0 0 0;
}
h6.calendar-block{
  color:#0f654a;
  border-bottom: 4px double #ccc;
  padding: 0 0 .5rem 0;
  margin: 0 0 .5rem 0;
  font-weight: 500;
  line-height: 1.3rem;
}
#story-content .calendar-feature-block p, #story-content .calendar-feature-block ul{
  font-size: .9rem;
  line-height: 1.2rem;
  margin-bottom: .1rem;
}
ul.calendar-list{
  margin: .3rem 0 .3rem 2rem;
}
div.calendar-feature-block + div.featured-content-block {
  margin: 2rem 0;
}

/*Active bar*/
#active-bar{
  height: auto;
  color: #333;
  position: relative;
  width: 100%;
  z-index: 10;
  padding: .8rem 0;
}
#containingbox.row{
  border-top: 1px solid #ccc;
  padding-top: 1rem;
}

ul.twitter-content{
  list-style: none;
  margin: 0 0 .6rem 0;
  color: #333;
  font-size: .9rem;
}
ul.twitter-content li{
  border-bottom: 1px dotted #ccc;
}
ul.twitter-content li:last-of-type{
  border-bottom: none;
}
ul.twitter-content li{
  display: block;
  padding: .4rem 0;
  color: #333;
  line-height: 1.4rem;
}
ul.twitter-content li:hover{
  text-decoration: none;
  background-color: #f8f8f8;
}
.twitterlink p{
  display: inline;
  float: left;
  margin: 0;
  padding: 0;
}
.twitterlink p:last-child{
  float: right;
}
#twitter-info, #calendar-info {
  margin-bottom: 1.2rem;
}
/*Calendar*/
ul.calendar-event-group{
  margin-bottom: 1rem;
  list-style: none;
}
ul.calendar-event-group a{
  color: #333;
}
#newshub-calendar-front li p.datecontent-box{
  font-size: .9rem;
  line-height: 1.1rem;
  margin-top: .4rem;
  margin-bottom: 0;
}
.date-box{
  box-sizing: border-box;
  text-align:center;
  background-color: #999;
  color: #fff;
  padding: .3rem 0 .6rem 0;
}
.date-box p:first-child{
  text-transform: uppercase;
  font-size: .7rem;
  padding: 0;
  margin: 0;
}
.date-box p:last-child{
  font-family: "PT Serif", serif;
  font-size: 1.2rem;
  line-height: 1.0rem;
  padding: 0;
  margin: 0;
}
#newshub-calendar-front li.calendar-unit {
  margin-bottom: .4rem;
}
.event-box{
  border-bottom: 4px double #ccc;
  padding:0;
  margin:0 0 .3rem 0;
}
.event-box h5{
  margin-top: 0;
}
#newshub-calendar-front p {
  margin: 0;
}
/*Base bar*/
#base-message-bar {
  background-color: #9d9c9c;
  width: 100%;
  color: #333;
}
#base-message-bar a{
  color: #333;
}
#base-message-bar p{
  font-size: .9rem;
  padding-top:.6rem;
  margin-bottom: .6rem;
}
#base-bar {
  background-color: #a9a9a9;
  width: 100%;
  color: #333;
}
#base-bar a{
  color: #333;
}
#base-bar p, #base-bar h6{
  font-size: .9rem;
  padding:0;
  margin:0;
  line-height: 1.2rem;
  color: #333;
}
#base-bar h6 {
  font-weight: 500;
}
img.bottom-logo{
  width: 100%;
  padding: 1rem 0 .8rem 0;
}
#linking-lists{
  padding-top: 1rem;
}
#linking-lists ul li{
  font-size:.9rem;
  list-style: none;
  line-height: 1.2rem;
  padding-left: 0;
}
#linking-lists ul li ul{
  margin: 0;
}
#social-links{
  padding-top: 1rem;
}
#final-bar {
  padding-bottom: 2rem;
}

/*announcement listing bar*/
#listing-bar{
  height: auto;
  color: #333;
  position: relative;
  width: 100%;
  z-index: 10;
  padding: .8rem 0 0 0;
}
ul.accordion {
  margin-left: 0;
  margin-bottom: .5rem;
  border-bottom: 1px dotted #ccc;
}
.accordion .accordion-navigation > a, .accordion dd > a {
  background: none;
  color: #0f654a;
  padding: .5rem 0rem .5rem 1.1rem;
  display: block;
  font-size: 1.1rem;
  text-decoration: none;
  border-top: 1px dotted #ccc;
  background-image: url(../images/dial-arrow-side.png);
  background-repeat: no-repeat;
  background-position: 0 1rem;
}
.accordion .accordion-navigation:first-of-type > a, .accordion dd:first-of-type > a {
  /*border-top: 4px double #ccc;*/
  border-top: none;
}
.accordion .accordion-navigation > a:hover, .accordion dd > a:hover {
  background: none;
  background-image: url(../images/dial-arrow-side.png);
  background-repeat: no-repeat;
  background-position: 0 1rem;
}
.accordion .accordion-navigation.active > a, .accordion dd.active > a {
  background: none;
  background-image: url(../images/dial-arrow-down.png);
  background-repeat: no-repeat;
  background-position: 0 1.1rem;
}
.accordion .accordion-navigation.active > a:hover, .accordion dd.active > a:hover {
  background: none;
  background-image: url(../images/dial-arrow-down.png);
  background-repeat: no-repeat;
  background-position: 0 1.1rem;
}
.accordion .accordion-navigation > .content, .accordion dd > .content {
  display: none;
  padding: 0rem 0 1rem 1.1rem;
  font-size: .9rem;
  line-height: 1.3rem;
}
.accordion .accordion-navigation > .content.active, .accordion dd > .content.active {
  display: block;
  background: none;
}
#title{}
#title-grouping, #base-grouping{
  border-bottom: 4px double #ccc;
}
#title-grouping:last-of-type, #base-grouping:last-of-type{
  border-bottom: none;
  margin-bottom: 1rem;
}
#title-grouping h6, #base-grouping h6{
  line-height: 1.6rem;
}
#title-grouping h3, #base-grouping h3{
  line-height: 1.4rem;
}

/*News Headlines*/
.publish-date {
  float: right;
  padding: .6rem 0 0 0;
  margin: 0;
  color: #999;
  font-size:.9rem;
}
ul.news-headlines {
  margin-left: 0;
  margin-bottom: 1rem;
  list-style: none;
}
ul.news-headlines a{
  color: #333;
  display: block;
  padding: .5rem 0;
  border-bottom: 1px dotted #ccc;
}
ul.news-headlines a:hover{
  text-decoration: none;
  background-color: #f8f8f8;
}
