  width: 100%;
  z-index: 10;
  padding: .8rem 0 0 0;
}
p.story-publish-date a, p.small-return-news a{
  font-size: .9rem;
  color: #666;
  padding-top: 1.6rem;
}
p.small-return-news {
  text-align: right;}

  #news-story-bar #title-grouping {
    border: none;
  }
  #story-content p{
    font-size: 1rem;
