  }
  #five-events-bar .calendar-content {
    margin: 0;
    padding: 0;
    /*padding: .5rem .8rem .8rem;*/
  }
  .calendar-text-content p {
    text-align: left;
  }
  #graybar{
    width: 100%;
    padding: .3rem 0;
    background-color: #bebdbd;
  }
  #graybar h4{
    text-transform: uppercase;
